import TransactionHistory from './TransactionHistory';
// import PropTypes from 'prop-types';

export default function TransactionList({ items }) {
  return (
    <ul>
      {items.map(item =>
        <li key={item.id}>
          <TransactionHistory
            key={item.id}
            type={item.type}
            amount={item.amount}
            currency={item.currency}
          />
        </li>
      )} 
    </ul>
  )
};

